

function OrderDetails(props) {

   const {title, price, image, quantity} = props;
   

    return <div className="order_list_details-item">
        <img style={{justifySelf:'start'}} width='100vw'  src={image} alt="order_item_image" />
        <p style={{fontWeight:'600'}}>{title}</p>
        <p style={{justifySelf:'start'}}> : <span style={{fontWeight:'600'}}>{price} .</span></p>
        <p>: <span style={{fontWeight:'600', justifySelf:'start'}}>{quantity}</span></p>
        <p> : <span style={{fontWeight:'600'}}> {quantity * price} .</span></p>
        </div>
}

export {OrderDetails}